@tailwind base;
@tailwind components;
@tailwind utilities;
@import url('https://fonts.googleapis.com/css?family=Josefin+Sans');

/* Add to your main CSS file (e.g., index.css) */
#toplevel_page_uaepro .wp-submenu li.wp-first-item::after,
#toplevel_page_uaepro .wp-submenu li:nth-last-child(2)::after {
	content: "";
	border-bottom: 1px solid rgba(255, 255, 255, 0.2);
	display: block;
	float: left;
	margin: 8px 0;
	width: 100%;
}
input[type="radio"].uael-radio-field:checked::before {
	content: "";
    border-radius: 50%;
    width: 0.5rem;
    height: 0.5rem;
    margin: 0.1875rem;
    background-color: #6005FF;
    line-height: 1.14285714;
}
input[type="radio"].uael-radio-field:focus {
	border-color: #6005ff;
	box-shadow: 0 0 0 1px #6005ff;
}
.loader {
	border: 4px solid #f3f3f3;
	border-radius: 50%;
	border-top: 4px solid #3498db;
	width: 40px;
	height: 40px;
	animation: spin 2s linear infinite;
}
.subject-cell {
	max-width: 200px; /* Adjust this width based on your layout */
	white-space: nowrap;
	overflow: hidden;
	text-overflow: ellipsis;
}
.uae-tooltip-wrap {
	width: 100%;
    text-align: right;
}

.uael-radio-option {
	width: 1rem;
	height: 1rem;
}
.uae-subheading h2 {
	font-size: 1.1rem !important;
}
.uae-subheading .text-text-secondary {
	font-size: 0.98rem !important;
}
.uae-loader-icon {
	top: -15%;
}
/* .uae-menu-page-wrapper {
	width: calc( 100% - 25px);
} */
#uae-settings-app .gap-2 {
    gap: 1.2rem;
}
.toplevel_page_uaepro #wpcontent {
    padding-left: 0;
}
div#uae-settings-app {
    font-size: 15px;
    font-weight: 500;
    line-height: normal;
}
.uael-settings-page h2 {
	margin-bottom: 0.3rem !important;
}
.uae-hide-scrollbar {
	overflow: hidden; /* Hide scrollbar */
}
.uae-hide-scrollbar::-webkit-scrollbar {
    display: none; /* Hide scrollbar for WebKit browsers */
}
.uae-hide-scrollbar {
    -ms-overflow-style: none;  /* Hide scrollbar for IE and Edge */
    scrollbar-width: none;  /* Hide scrollbar for Firefox */
}
.uael-link-color,
.uael-link-color span {
	color: rgb(96, 5, 255);
}
/* #uae-settings-app .p-5 {
    padding: 0.5rem 1.25rem;
} */
#uae-settings-app .uae-header-title h6 {
	color: #858484;
	font-size: 14px;
}
.uae-settings-content-wrapper {
	padding: 15px 25px;
}
button.uae-plugin-activated {
	color: #16A34A;
}
button.uae-bulk-action-button {
	width: 150px;
}
.uae-subtitle h2 {
	font-size: 0.90rem;
}
.uael-65-width {
	width: 65%;
}
.uael-35-width {
	width: 35%;
}
.uael-78-width {
	width: 78%;
}

.uael-remove-ring input[type="checkbox"],
.uael-remove-ring a,
.uael-remove-ring {
    outline: none; /* Removes the outline */
    box-shadow: none !important; /* Ensures no shadow on focus */
    --tw-ring-inset: auto; /* Tailwind-specific variable, keep if needed */
}
.uael-remove-ring a:focus,
.uael-remove-ring input[type="checkbox"]:focus {
    outline: none; /* Ensures focus does not trigger the outline */
    box-shadow: none !important; /* Safeguards against custom focus shadows */
}
.uael-green-placeholder::placeholder {
	color: #1ca51c;
}
@keyframes spin {
	0% {
		transform: rotate(0deg);
	}
	100% {
		transform: rotate(360deg);
	}
}
@media (min-width: 769px) {
	.uae-sticky-outer-wrapper {
		/* position: absolute;
		height: -webkit-fill-available;
		top: inherit; */
		width: 23%;
		border-right: 1px solid #e5e7eb;
	}
	.uae-sticky-sidebar {
		position: sticky;
		top: 0;
	}
	.uae-settings-page:before {
		animation-fill-mode: both;
		background: #fff;
		content: " ";
		height: 100%;
		position: fixed;
		left: 0;
		top: 0;
		transform-origin: right;
		border-right: 1px solid #e5e7eb;
		width: 30%;
	}
	.uae-nav-menu {
		border-bottom: 1px solid #e5e7eb;
	}
}

@media (max-width: 768px) {
	.container {
		padding: 0 1rem;
	}

	.fixed {
		justify-content: flex-start !important;
	}

	.bg-white {
		width: 100% !important;
		height: 100% !important;
		border-radius: 0 !important;
	}
}

@media (max-width: 1024px) {
	.uael-65-width,
	.uael-35-width,
	.uael-78-width {
		width: 100%;
	}
}

@media screen and (max-width: 782px) {
	.uael-radio-option {
		width: 1rem !important;
		height: 1rem !important;
	}
	.uael-radio-option:checked:before {
		width: 0.5rem !important;
        height: 0.5rem !important;
        margin: 0.18rem !important;
	}
}